import React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import Masonry from "react-responsive-masonry";

import Card from 'react-bootstrap/Card';
import estampillaTitle from '../../../assets/estampilla-1.jpg';
import fotos from '../../../assets/fotos';

export const FotosScreen = () => {
    return (
        <>
            <Container fluid="md">
                <Row>
                    <Col>
                        <Card>
                            <Card.Body>
                                <Card.Title style={{ color: "#A4133C" }}><b>FOTITOS</b> <img src={estampillaTitle} style={{ width: "35px", paddingBottom: "0.28rem", transform: [{ rotate: '45deg' }] }} /></Card.Title>
                                <hr></hr>
                                <Card.Text>
                                    <Row>
                                        <Masonry columnsCount={3} gutter={24}>
                                            {fotos.map((image, key) => (
                                                <img src={image} key={key} />
                                            ))}
                                        </Masonry>
                                    </Row>
                                </Card.Text>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Container>
        </>
    )
}