import React, { useEffect, useState } from 'react';
import { Route, Routes, useLocation } from 'react-router-dom';
import { CancionesScreen } from '../../domain/Canciones/CancionesScreen';
import { FotosScreen } from '../../domain/Fotos/FotosScreen';
import { NosotrosScreen } from '../../domain/Nosotros/NosotrosScreen';
import "./styles.css";

const MainContent = () => {
    const location = useLocation();
    const [displayLocation, setDisplayLocation] = useState(location);
    const [transitionStage, setTransistionStage] = useState("fadeIn");

    useEffect(() => {
        if (location !== displayLocation) setTransistionStage("fadeOut");
    }, [location]);
    return (
        <>
            <div className="main-content" style={{
                backgroundColor: "#FFCCD5", paddingTop: 30, paddingBottom: "100%", height: "100%",
                width: "100%"
            }}>
                <div
                    className={`${transitionStage}`}
                    onAnimationEnd={() => {
                        if (transitionStage === "fadeOut") {
                            setTransistionStage("fadeIn");
                            setDisplayLocation(location);
                        }
                    }}>
                    <Routes>
                        <Route path="nosotros" element={<NosotrosScreen />} />
                        <Route path="fotos" element={<FotosScreen />} />
                        <Route path="canciones" element={<CancionesScreen />} />
                    </Routes>
                </div>
            </div>
        </>
    )
}

export default MainContent;