import React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import Card from 'react-bootstrap/Card';
import estampillaTitle from '../../../assets/estampilla-1.jpg';
import YouTube from 'react-youtube';

export const CancionesScreen = () => {
    const opts = {
        height: '280',
        width: '390',
        playerVars: {
            // https://developers.google.com/youtube/player_parameters
            autoplay: 1,
        },
    };

    const _onReady = (event) => {
        // access to player in all event handlers via event.target
        event.target.pauseVideo();
    };

    return (
        <>
            <Container fluid="md">
                <Row>
                    <Col>
                        <Card>
                            <Card.Body>
                                <Card.Title style={{ color: "#A4133C" }}><b>CANCIONES</b> <img src={estampillaTitle} style={{ width: "35px", paddingBottom: "0.28rem", transform: [{ rotate: '45deg' }] }} /></Card.Title>
                                <hr></hr>
                                <Card.Text>
                                    <Row>
                                        <Col>
                                            <YouTube videoId="DxUJPrSrTRk" opts={opts} onReady={_onReady} />
                                        </Col>
                                        <Col>
                                            <YouTube videoId="nOXS1P-_2ic" opts={opts} onReady={_onReady} />
                                        </Col>
                                        <Col>
                                            <YouTube videoId="Tmp1uUetucQ" opts={opts} onReady={_onReady} />
                                        </Col>
                                    </Row>
                                    <br></br>
                                    <br></br>
                                    <Row>
                                        <Col>
                                            <YouTube videoId="sjqvcVuoNmU" opts={opts} onReady={_onReady} />
                                        </Col>
                                        <Col>
                                            <YouTube videoId="sTHvW6dAdVw" opts={opts} onReady={_onReady} />
                                        </Col>
                                        <Col>
                                            <YouTube videoId="XEycpdyxOUs" opts={opts} onReady={_onReady} />
                                        </Col>
                                    </Row>
                                </Card.Text>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Container>
        </>
    )
}